<script>
import DataList from "@/components/datalist";
import fileToBase64 from "@/utils/fileToBase64";
export default {
    name: "auto-control-list",
    components: { DataList },
    data() {
        return {
            layer: false,
            filter: {
                entrustNo: "",
                graphNo: "",
                inspectStatus: "-1",
                status: "-1",
                type: ""
            },
            rowStatusList: [
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" }
            ],
            inspectStatusList: { 0: "", 1: "", 2: "" },
            statusList: [
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" },
                { status: -1, name: "" }
            ],
            inspectHistory: [],
            busTypeList: ["", "", ""],
            spray: {
                planner: "",
                sprayNo: "",
                createTime: "",
                items: []
            },
            workTypeArr: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            workTypeList: [
                {
                    text: "",
                    value: ""
                },
                { text: "", value: "" },
                { text: "", value: "" },
                { text: "", value: "" },
                {
                    text: "",
                    value: ""
                },
                {
                    text: "",
                    value: ""
                },
                {
                    text: "",
                    value: ""
                },
                {
                    text: "",
                    value: ""
                }
            ],
            addLayer: false,
            form: {
                graphNo: "",
                id: "",
                model: "",
                name: "",
                qty: 0,
                specifications: "",
                workType: ""
            },
            loading: false,
            fileList: [],
            completeLayer: false,
            inspectHistoryAdd: {
                accessoryList: [
                    {
                        fileName: "",
                        fileUrl: ""
                    }
                ],
                autoControlId: "",
                handlingSuggestion: "",
                materialGraphName: "",
                materialGraphNo: "",
                no: "",
                qualifiedNumber: 0,
                reasonList: [
                    {
                        number: 0,
                        reason: ""
                    }
                ],
                remark: "",
                testNumber: 0,
                unqualifiedNumber: 0
            }
        };
    },
    methods: {
        flush() {
            this.filter = {
                entrustNo: "",
                graphNo: "",
                inspectStatus: "-1",
                status: "-1",
                type: ""
            };
            this.$refs.list.update(true);
        },
        add() {
            this.addLayer = true;
        },
        edit(item) {
            this.addLayer = true;
            this.form = {
                graphNo: item.graphNo,
                id: item.id,
                model: item.model,
                name: item.name,
                qty: item.qty,
                specifications: item.specifications,
                workType: item.workType + ""
            };
        },
        save() {
            this.loading = true;
            let url = this.form.id
                ? "/haolifa/auto-control-entrust/update"
                : "/haolifa/auto-control-entrust/add";
            this.$http
                .post(url, this.form)
                .then(res => {
                    this.close();
                    this.$toast("");
                    this.loading = false;
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.addLayer = false;
            this.form = {
                graphNo: "",
                id: "",
                model: "",
                name: "",
                qty: 0,
                specifications: "",
                workType: ""
            };
        },
        addSprayInspect(item) {
            this.inspectHistoryAdd = {
                accessoryList: [
                    {
                        fileName: "",
                        fileUrl: ""
                    }
                ],
                autoControlId: item.id,
                handlingSuggestion: "",
                materialGraphName: item.name,
                materialGraphNo: item.graphNo,
                no: item.entrustNo,
                qualifiedNumber: 0,
                reasonList: [
                    {
                        number: 0,
                        reason: ""
                    }
                ],
                remark: "",
                testNumber: 0,
                unqualifiedNumber: 0
            };
            this.fileList = [];
            this.sprayInfo(item);
            this.completeLayer = true;
        },
        updateStatus(sprayNo, status) {
            this.$http
                .put(`/haolifa/spray/status/${sprayNo}/${status}`)
                .then(res => {
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        detail(item) {
            this.sprayInfo(item);
            this.layer = true;
        },
        // 
        sprayInfo(item) {
            // 
            this.$http
                .post(`/haolifa/auto-control-entrust/detail/${item.id}`)
                .then(res => {
                    this.spray = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
            // 
            this.$http
                .get(
                    `/haolifa/auto-control-entrust/inspect/list/${
                        item.entrustNo
                    }`
                )
                .then(res => {
                    this.inspectHistory = res;
                    this.inspectHistory.map(item => {
                        return (item.reasonArr = item.reasonList.map(obj => {
                            if (obj.number)
                                return (
                                    ":" +
                                    obj.number +
                                    ",:" +
                                    obj.reason +
                                    ";"
                                );
                        }));
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        addReason() {
            this.inspectHistoryAdd.reasonList.push({ number: "0", reason: "" });
        },
        removeReason(index) {
            this.inspectHistoryAdd.reasonList.splice(index, 1);
        },
        complete() {
            this.loading = true;
            this.$http
                .post(
                    `/haolifa/auto-control-entrust/inspect`,
                    this.inspectHistoryAdd
                )
                .then(res => {
                    this.loading = false;
                    this.$toast("");
                    this.completeLayer = false;
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.loading = false;
                    this.$toast(e.msg || e.message);
                });
        },
        uploadFile(file, fileList) {
            this.loading = true;
            fileToBase64(file.source).then(base64Str => {
                this.$http
                    .post("/haolifa/file/uploadFileBase64", {
                        base64Source: base64Str,
                        fileName: file.name
                    })
                    .then(res => {
                        this.inspectHistoryAdd.accessoryList.push({
                            fileName: file.name,
                            fileUrl: res
                        });
                        this.loading = false;
                    })
                    .catch(e => {
                        this.$toast(e.msg || e.message);
                        this.loading = false;
                    });
            });
        },
        removeFile(fileList, index) {
            return new Promise((resolve, reject) => {
                this.inspectHistoryAdd.accessoryList.splice(index, 1);
                resolve();
            });
        },
        //
        machine(item) {
            this.updateStatus(item.id, 1);
        },
        // 
        stopMachine(item) {
            this.updateStatus(item.id, 4);
        },
        // 
        completeMachine(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.entrustNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    if (item.qualifiedNumber < item.qty) {
                        let count = item.qty - item.qualifiedNumber;
                        this.$confirm({
                            title: "",
                            text:
                                "(" +
                                count +
                                ")",
                            color: "red",
                            btns: ["", ""],
                            yes: () => {
                                this.updateStatus(item.id, 3);
                            }
                        });
                    } else {
                        this.updateStatus(item.id, 3);
                    }
                }
            });
        },
        updateStatus(id, status) {
            this.$http
                .put(`/haolifa/auto-control-entrust/status/${id}/${status}`)
                .then(res => {
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        del(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.entrustNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .post(`/haolifa/auto-control-entrust/del/${item.id}`)
                        .then(res => {
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        }
    }
};
</script>

