<script>
import DataList from "@/components/datalist";
export default {
    name: "spray-produce-list",
    components: { DataList },
    data() {
        return {
            layer: false,
            filter: {
                status: -1,
                type: 0
            },
            rowStatusList: [
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" }
            ],
            busTypeList: ["", "", ""],
            inspectStatusList: { 0: "", 1: "", 2: "" },
            statusList: [
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" },
                { status: -1, name: "" }
            ],
            inspectHistory: [],
            spray: {
                planner: "",
                sprayNo: "",
                createTime: "",
                items: []
            }
        };
    },
    methods: {
        flush() {
            this.filter = {
                status: -1,
                type: 0
            };
            this.$refs.list.update(true);
        },
        downloadForm(item) {
            this.$http.get(`/haolifa/export/spray/excel/${item.sprayNo}`);
        },
        // 
        editForm(item) {
            this.$router.push({
                path: `/spray/edit?`,
                query: { sprayNo: item.sprayNo }
            });
        },
        // 
        deleteInfo(item) {
            this.$http
                .delete(`/haolifa/spray/form/${item.sprayNo}`)
                .then(res => {
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        //
        machine(item) {
            if (item.outRoomStatus != 2) {
                this.$toast("");
                return;
            }
            this.updateStatus(item.sprayNo, 1);
        },
        // 
        stopMachine(item) {
            this.updateStatus(item.sprayNo, 4);
        },
        // 
        completeMachine(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.sprayNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    if (item.qualifiedNumber < item.totalNumber) {
                        let count = item.totalNumber - item.qualifiedNumber;
                        this.$confirm({
                            title: "",
                            text:
                                "(" +
                                count +
                                ")",
                            color: "red",
                            btns: ["", ""],
                            yes: () => {
                                this.updateStatus(item.sprayNo, 3);
                            }
                        });
                    } else {
                        this.updateStatus(item.sprayNo, 3);
                    }
                }
            });
        },
        updateStatus(sprayNo, status) {
            this.$http
                .put(`/haolifa/spray/status/${sprayNo}/${status}`)
                .then(res => {
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        // 
        sprayInfo(item) {
            // 
            this.$http
                .get(`/haolifa/spray/form/${item.sprayNo}`)
                .then(res => {
                    this.spray = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
            // 
            this.$http
                .get(`/haolifa/spray/inspect/list/${item.sprayNo}`)
                .then(res => {
                    this.inspectHistory = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
            this.layer = true;
        }
    }
};
</script>

