<template>
    <div class="page-material-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                
                <select v-model="filter.status" class="f-14" @change="$refs.list.update(true)">
                    <option v-for="item in statusList" :value="item.status" v-bind:key="item.id">{{item.name}}</option>
                </select>
                <i class="icon" style="margin-left: -20px;pointer-events:none;">arrow_drop_down</i>
            </div>
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/entrust/list" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <!--<th></th>-->
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{index}}</td>
                    <td>{{item.entrustNo}}</td>
                    <td>{{item.purchaseNo}}</td>
                    <td>{{item.batchNumber}}</td>
                    <td>{{workShopList[item.workshopType]}}</td>
                    <td>{{item.materialGraphName}}</td>
                    <td>{{item.materialGraphNo}}</td>
                    <td>{{item.processedGraphNo}}</td>
                    <td>{{busTypeList[item.busType]}}</td>
                    <td>{{item.specifications}}</td>
                    <td>{{item.model}}</td>
                    <td>{{item.number}}</td>
                    <td>{{item.qualifiedNumber}}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{rowStatusList[item.status-1].name}}</td>
                    <!--<td>{{inspectStatusList[item.inspectStatus]}}</td>-->
                    <td class="t-right">
                        <a href="javascript:;" v-if="item.status == 1" style="margin-right: 3px" class="blue" @click="edit(item)"> |</a>
                        <a href="javascript:;" v-if="item.status == 1" style="margin-right: 3px" class="blue" @click="audtiNotPass(item)"> |</a>
                        <a :href="`/haolifa/export/entrust/excel/${item.entrustNo}`" download style="margin-right: 3px" class="blue"></a>
                    </td>
                </template>
            </data-list>
        </div>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "page-machining-list",
    components: { DataList },
    data() {
        return {
            filter: {
                type: 1,
                status: -1
            },
            inspectStatusList: { 0: "", 1: "", 2: "" },
            rowStatusList: [
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" },
                { status: 5, name: "" },
                { status: 6, name: "" }
            ],
            statusList: [
                { status: -1, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" },
                { status: 3, name: "" },
                { status: 4, name: "" },
                { status: 5, name: "" },
                { status: 6, name: "" }
            ],
            busTypeList: ["", "", ""],
            workShopList: ["", "1", "", "2"]
        };
    },
    methods: {
        flush() {
            this.filter = {
                type: 1,
                status: -1
            };
            this.$refs.list.update(true);
        },
        audtiNotPass(item) {
            this.$confirm({
                title: "",
                text: "",
                color: "blue",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(
                            `/haolifa/entrust/updateStatus/${item.entrustNo}/5`
                        )
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        });
                }
            });
        },
        edit(item) {
            this.$router.push(`/workcar/edit?id=${item.id}`);
        }
    }
};
</script>

