<template>
    <div class="page-room-add abs scroll-y">
        <div class="form-content">
            <div class="title b f-18"></div>
            <div class="flex-v-center">
                <select-box v-model="form.workShopType" class="mr-10" :list="workcarList" style="width:100%" label=""></select-box>
            </div>
            <div class="flex-v-center" v-show="form.workShopType == 2">
                <input-box v-model="form.supplierName" class="mr-10" label="" style="width: 50%" type="text"></input-box>
                <!--<input-box-->
                <!--v-model="form.supplierNo"-->
                <!--class="flex-item mr-10"-->
                <!--label=""-->
                <!--&gt;</input-box>-->
            </div>
            <div class="flex-v-center" style="margin: 20px 0;">
                <btn big class="mr-20" @click="submit"></btn>
                <btn big flat bg class="mr-20" @click="cancel"></btn>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "workcar-add",
    data() {
        return {
            workcarList: [
                { text: "1", value: 1 },
                { text: "2", value: 3 },
                { text: "", value: 2 }
            ],
            form: {
                id: "",
                workShopType: "",
                supplierName: "",
                supplierNo: ""
            }
        };
    },
    computed: {
        // canSubmit () {
        //   const { form } = this
        //   return form.materialName && form.materialGraphNo && form.materialCount && form.orderNo && form.materialUnit && form.responsiblePerson && form.replaceReason
        // }
    },
    created() {
        let { id } = this.$route.query;
        this.form.id = id;
    },
    activated() {
        this.form = {
            id: "",
            workShopType: "",
            supplierName: "",
            supplierNo: ""
        };
        let { id } = this.$route.query;
        this.form.id = id;
    },
    methods: {
        cancel() {
            this.$confirm({
                title: "",
                text: "",
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$store.commit("DELMENUTABS", "/workcar/edit");
                    this.$router.push("/workcar");
                }
            });
        },
        submit() {
            if (!this.form.workShopType) {
                this.$toast("");
                return;
            }
            const { form } = this;
            this.loading = true;
            const method = "post";
            this.$http[method](`/haolifa/entrust/allotEntrust`, form)
                .then(res => {
                    this.loading = false;
                    this.$store.commit("DELMENUTABS", "/workcar/edit");
                    this.$router.push("/workcar");
                })
                .catch(e => {
                    this.loading = false;
                    this.$toast(e.msg || e.message);
                });
        }
    }
};
</script>

<style lang="less">
.page-room-add {
    padding: 30px 20px;
    .title {
        margin-bottom: 20px;
    }
}
</style>
